}

.onion-btn a.tpgb-btn-link {
  display: inline-block;
  color: var(--white);
  border-radius: 3.125rem;
  padding: 0.75rem 1.875rem;
  font-size: 0.875rem;
  cursor: pointer;
  background: linear-gradient(90deg, rgb(211, 28, 119) 0%, rgb(32, 169, 225) 100%);
  position: relative;
  font-weight: var(--font-sb);
  z-index: 1;
  transition: all 0.5s;
  text-transform: uppercase;
  width: fit-content;
  white-space: nowrap;
}
.onion-btn a.tpgb-btn-link .tpgb-btn-wrap {
  background: transparent;
  padding: 0;
}

.onion-btn-border a.tpgb-btn-link {
  display: inline-block;
  color: #303030;
  border-radius: 3.125rem;
  padding: 0.75rem 1.875rem;
  font-size: 0.875rem;
  cursor: pointer;
  background: transparent;
  position: relative;
  font-weight: var(--font-sb);
  z-index: 1;
  transition: all 0.5s;
  text-transform: uppercase;
  width: fit-content;
  white-space: nowrap;
  border: 1px solid #D31C77;
}
.onion-btn-border a.tpgb-btn-link .tpgb-btn-wrap {
  background: transparent;
  padding: 0;
  color: #303030;
}

.scroll-btn {
  display: flex;
  align-items: center;
  flex-direction: column;
  gap: 0.625rem 0;
  position: absolute;
  bottom: 4.6875rem;
  left: 5.125rem;
  cursor: pointer;
}
.scroll-btn p {
  font-size: 0.875rem;
  line-height: 1.875rem;
  color: var(--a-hover);
  text-transform: capitalize;
  transform: rotate(270deg);
}
.scroll-btn .scroll-arrow {
  width: 0.9375rem;
}

.mob-cls {
  display: none;
}

footer {
