@keyframes clickIt {
  from {
    box-shadow: 0 3px 1px -2px rgba(255, 255, 255, 0.2196078431), 0 2px 2px rgba(255, 255, 255, 0.1803921569), 0 1px 5px rgba(255, 255, 255, 0.1607843137);
